export default {
	data() {
		return {
			ListFlag:false,
			time:false,
			listArr:[]
		}
	},
	onLoad(options) {
		this.getListArr()
	},
	onShow(){
		
	},
	methods: {
		getListArr:function(){
			let that=this;
				let usurl=that.url1+"/dinghai/order/selectMyOrder";
				uni.request({
					url: usurl,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						useruid:that.user.uid
					},
			    success: (res) => {
					if(res.statusCode==200){
						that.listArr=res.data.flag
						console.log(that.listArr)
					}else{
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
			    },
				fail : function(err){
					uni.showToast({
						title:"您的网络打了个盹",
						icon:"none"
					})
				}
			});
		},
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		
		   //  点击整体时显示功能
		showgn : function(){
			
		}
	}
}
